body{
  margin-top: 3px;
  padding: 1px;
  box-sizing: border-box;
  background: #141414;
}
h1 {
  text-align: center;
  color: #fff;
}
.main {
  justify-content: center;
}
.tagcloud {
  color: #fff;
  font-family: 'Poppins', sans-serif;  
  font-size: 20px;
  font-weight: 650;
  margin-left: 30%;
}
.tagcloud--item:hover {
  color: #c79b23 
}

@media (max-width: 800px) {
  .tagcloud {
    margin: 0%;
  }
  .h1 {
    margin-left: 50%;
    justify-content: center;
  }
}

